import React from 'react';

export const ViolList = (props) => {
  const violType = () => {
    const leadAndCopperCodes = [57, 58, 59, 63, 64, 65, 51, 52, 53, 56, 66];
    const tcr = [21, 22, 23, 24, 25, 26];
    const disinfectant = [11, 12, 13, 46, 27, 30, 35];
    const swr = [33, 37, 41, 42, 43, 44, 45, 47];
    if (props.violation.CONTAMINANT_CODE === '0700') {
      return 'Ground Water Rule';
    }
    else if (props.violation.CONTAMINANT_CODE === '0200' ||
      props.violation.CONTAMINANT_CODE === '0800' ||
      (props.violation.CONTAMINANT_CODE !== '0700' &&
        swr.includes(props.violation.VIOLATION_CODE))) {
      return 'Surface Water Rule';
    }
    else if (leadAndCopperCodes.includes(props.violation.VIOLATION_CODE)) {
      return 'Lead and Copper Rule';
    }
    else if (tcr.includes(props.violation.VIOLATION_CODE)) {
      return 'Total Coliform Rule';
    }
    else if (tcr.includes(props.violation.VIOLATION_CODE)) {
      return 'Total Coliform Rule';
    }
    else if (disinfectant.includes(props.violation.VIOLATION_CODE)) {
      return 'Disinfectant/ Disinfectant Byproduct Rule';
    }
    else {
      return 'Unknown';
    }
  };
  return (<tr className="violationInfo">
    <td>{props.facData.PWSID}</td>
    <td>{props.facData.PWS_NAME}</td>
    <td>{props.facData.ADDRESS_LINE1}</td>
    <td>{props.facData.CITY_NAME}</td>
    <td>{props.facData.STATE_CODE}</td>
    <td>{props.facData.ZIP_CODE}</td>
    <td>Yes</td>
    <td>WQ. Water Quality Management</td>
    <td>{props.facilityType()}</td>
    <td>Violation No. {props.violation.VIOLATION_ID}, {props.violation.IS_MAJOR_VIOL_IND === 'Y' ? 'Major' : 'Minor'}, {props.violation.VIOLATION_CATEGORY_CODE}</td>
    <td>{props.violation.PRIMACY_AGENCY_CODE}</td>
    <td>{violType()}</td>
  </tr>);
};
