import React from 'react';
import { Form, Row, Button } from 'react-bootstrap';
const requestPromise = require('request-promise');
const months = ["JAN", "FEB", "MAR", "APR", "MAY", "JUN", "JUL", "AUG", "SEP", "OCT", "NOV", "DEC"];

export const FacilityRetrieve = (props) => {
  const currDate = new Date();
  return (
  <Form onSubmit={event => props.retrieveInfo(event)} style={{ width: "90%" }}>
    <Form.Row>
      <Form.Label>
        Enter Comma Separated List of Facilities:
        </Form.Label>
    </Form.Row>
    <Form.Row>
      <Form.Control 
        name="facList" 
        type="text" 
        value={props.facList} 
        onChange={event => props.handleUserInput(event)} 
        style={{ width: "100%" }} 
      />
    </Form.Row>
    <Form.Row>
      <Form.Label>
        How far back would you like to search?
      </Form.Label>
    </Form.Row>
    <Form.Row>
      <Form.Control 
        name="month" 
        as="select" 
        value={props.month} 
        onChange={event => props.handleUserInput(event)} 
        style={{ width: "25%" }} 
      >
        {months.map((month, index) => {return (<option key={index}>{month}</option>)})}
      </Form.Control>
      <Form.Control 
        name="year" 
        as="select" 
        value={props.year} 
        onChange={event => props.handleUserInput(event)} 
        style={{ width: "25%" }} 
      >
        <option>{currDate.getFullYear()}</option>
        <option>{currDate.getFullYear()-1}</option>
        <option>{currDate.getFullYear()-2}</option>
        <option>{currDate.getFullYear()-3}</option>
        <option>{currDate.getFullYear()-4}</option>
      </Form.Control>
    </Form.Row>
    <Row style={{ "justifyContent": "center" }}>
      {!props.submitted ? <Button type="submit" size="sm">Submit</Button> : <Button type="submit" size="sm" disabled>Waiting</Button>}
    </Row>
  </Form>
  );
};

export const getFacilities = (facListIn, date) => {
  //1. Remove all spaces and create array of facilities
  const facList = facListIn.replace(/\s/g, '');
  const facArr = facList.split(',');
  //2. Call function to retrieve facility data
  //2a. Each facility will return its own promise
  //2a. Will need to use Promise.all to make sure all promises are resolved
  const tempArr = Promise.all(facArr.map((facNum) => {
    return getIndData(facNum, date, 1);
  }));
  return tempArr;
  //3. Convert each individual facilities data to an array
  //4. Return array of facility objects
};

const getIndData = async (facNum, date, tries) => {
  const retry = 4;
  //check to make sure data exists
  try {
    //1. set up the request URL
    const requestURL = `https://iaspub.epa.gov/enviro/efservice/WATER_SYSTEM/PWSID/=/${facNum}/VIOLATION/COMPL_PER_BEGIN_DATE/>/${date}/JSON`;
    //3. call database
    const queryDataJSON = await requestPromise({ uri: requestURL });
    const queryData = JSON.parse(queryDataJSON);
    if (queryData.length===0){
      return [{PWSID: facNum, failed: true}];
    } else {return queryData}
  }
  catch (error) {
    if (tries < retry) {
      return getIndData(facNum, date, tries + 1);
    }
    else {
      console.log(`Attempt on ${facNum}'s data ended in failure.`);
      return [{PWSID: facNum, failed: true}];
    }
  }
};
