import React from 'react';
const requestPromise = require('request-promise');

export class Enforcement extends React.Component {
  constructor(props){
    super (props)
    this.state = {
      checking:false,
    }
  }

  handleClick= async ()=>{
    if (!this.state.checking){
      //1. call epa database for enforcement details
      const dataReturn = await getEnfData(this.props.pwsId, this.props.enfId);
      const epaData = dataReturn[0]
      //2. put the return info into the current state
      this.setState({epaData})
      //3. data should be displayed
    }
  }

  render() {
    if(this.state.epaData){
      const enfData = this.state.epaData
      return (
        <tr>
          <td style={{backgroundColor:"Blue"}}></td>
          <td colSpan="1">Originator: {enfData.ORIGINATOR_CODE==='S' ? 'State' : 'EPA Region'}</td>
          <td colSpan="1">Date: {enfData.ENFORCEMENT_DATE}</td>
          <td colSpan="2">Action: {enfActions[enfData.ENFORCEMENT_ACTION_TYPE_CODE]}</td>
          <td colSpan="6">Comment: {enfData.ENFORCEMENT_COMMENT_TEXT}</td>
          <td style={{backgroundColor:"Blue"}}></td>
        </tr>
      )
    } else {
      return (
        <tr onClick={event=>this.handleClick()}>
          <td style={{backgroundColor:"Blue"}}></td>
          <td colSpan="10" className="enf">Click to view Enforcement Details</td>
          <td style={{backgroundColor:"Blue"}}></td>
        </tr>
      );
    }
  }
}

const enfActions ={"SF%":"Civil Case concluded", "SF&":"Crim Case referred to AG", "SF2":"Referred for Higher St Level Review ", "SF3":"Case appealed ", "SF4":"Case dropped ", "SF5":"Hook-up/Extension Ban ", "SF9":"Civil Case referred to AG", "SFG":"Public Notif issued", "SFH":"Boil Water Order", "SFJ":"Formal NOV issued", "SFK":"BCA signed ", "SFL":"AO (w/o penalty) issued ", "SFM":"Admin Penalty assessed", "SFN":"Show-cause Hearing ", "SFO":"AO (w/penalty) issued", "SFP":"Civil Case under development ", "SFQ":"Civil Case filed", "SFR":"Consent Decree/Judgement ", "SFS":"Default Judgement ", "SFT":"Injunction", "SFU":"Temp Restrain Order/Prelim Injunc ", "SFV":"Crim Case filed", "SFW":"Crim Case concluded", "SIA":"Violation/ Reminder Notice", "SIB":"Compliance Meeting conducted", "SIC":"Tech Assistance Visit", "SID":"Site Visit (enforcement)", "SIE":"Public Notif requested", "SIF":"Public Notif received", "SII":"CCR Follow-up Notice", "SO+":"No addtl Formal Action needed", "SO0":"No Longer Subject to Rule", "SO6":"Intentional no-action ", "SO7":"Unresolved", "SO8":"Other", "SOX":"Compliance achieved ", "SOY":"Variance/ Exemption issued", "SOZ":"St Turbidity Waiver issued ", "EF-":"CFP Consent Order/Decree w/penalty ", "EF!":"PAO issued ", "EF%":"Fed Civil Case concluded ", "EF&":"Fed Crim Case referred to DOJ ", "EF/":"Fed 1431 (Emergency) Order ", "EF<":"Fed CFP issued", "EF=":"Fed CFP Default Judgement ", "EF2":"Referred for Higher Level Review", "EF9":"Civil Case referred to DOJ", "EFG":"Public Notif issued", "EFH":"Boil Water Order", "EFJ":"Formal NOV issued", "EFK":"BCA signed ", "EFL":"FAO issued ", "EFN":"Show-cause Hearing ", "EFQ":"Civil Case filed", "EFR":"Consent Decree/ Judgement", "EFS":"Default Judgement ", "EFT":"Injunction", "EFU":"Temp Restrain Order/Prelim Injunc ", "EFV":"Crim Case filed", "EFW":"Crim Case concluded", "EIA":"Violation/ Reminder Notice", "EIB":"Compliance Meeting conducted", "EIC":"Tech Assistance Visit", "EID":"Site Visit (enforcement)", "EIE":"Public Notif requested", "EIF":"Public Notif received", "EII":"CCR Follow-up Notice", "EO+":"No addtl Formal Action needed", "EO0":"No Longer Subject to Rule", "EO6":"Intentional no-action ", "EO7":"Unresolved", "EO8":"Other", "EOX":"Compliance achieved ", "EOY":"Variance/ Exemption issued", "EOZ":"Turbidity Waiver issued", "ESX":"Implicit SWTR RTC (Cannot be reported, EPA-generated)", "ETX":"Implicit TCR RTC (Cannot be reported, EPA-generated)", }

const getEnfData = async (pwsId, enfId, tries) => {
  const retry = 2;
  //check to make sure data exists
  try {
    //1. set up the request URL
    const requestURL = `https://iaspub.epa.gov/enviro/efservice/ENFORCEMENT_ACTION/ENFORCEMENT_ID/=/${enfId}/PWSID/=/${pwsId}/JSON`;
    //3. call database
    const queryDataJSON = await requestPromise({ uri: requestURL });
    const queryData = JSON.parse(queryDataJSON);
    return queryData;
  }
  catch (error) {
    if (tries < retry) {
      return setTimeout(getEnfData(pwsId, enfId, tries+1), 2000);
    }
    else {
      console.log(`Attempt on ${enfId}'s data ended in failure.`);
      return [];
    }
  }
};