import React from 'react';
import { Table, Form, Button } from 'react-bootstrap'
import './App.css';
import { getFacilities, FacilityRetrieve } from './components/FacilityRetrieve';
import { FacilityDisplay } from './components/FacilityDisplay';

class App extends React.Component {
  constructor (props) {
    super(props)
    this.state={
      submitted: false,
      receivedInfo: false,
      facList: '',
      month: 'JAN',
      year: new Date().getFullYear(),
    }
  }

  handleUserInput = (event) => {
    const target = event.target;
    const name = target.name;
    const value = target.value;

    this.setState({
      [name]: value
    });
  }

  retrieveInfo = async (event) => {
    this.setState({submitted:true})
    event.preventDefault();
    const date = `01-${this.state.month}-${this.state.year}`
    const facilityInfo = await getFacilities(this.state.facList, date);
    this.setState({facilityInfo, receivedInfo:true, submitted:false});
  }

  render() {
    if (this.state.receivedInfo){
      return (
        <div className="App">
          <header className="App-header">
            <h1>Jason's EPA Database Aggregator.</h1>
            <Reset />
          </header>
          <main>
            <Table bordered striped size="sm" >
              <TableHeader/>
              <FacilityDisplay 
                facilityInfo={this.state.facilityInfo}
                failedFacilities={this.failedFacilities}
              />
            </Table>
          </main>
        </div>
      );      
    } else {
      return (
        <div className="App">
          <header className="App-header">
            <h1>Jason's EPA Database Aggregator.</h1>
            <br/>
            <FacilityRetrieve 
              retrieveInfo={this.retrieveInfo} 
              submitted={this.state.submitted} 
              handleUserInput={this.handleUserInput} 
              facList={this.state.facList}
              beginDate={this.state.beginDate}
            />
          </header>
        </div>
      );
    }
  }
}

const TableHeader = () => {
  return (
    <thead>
      <tr>
        <td>PWS ID</td>
        <td>Facility Name</td>
        <td>Street Address</td>
        <td>City Address</td>
        <td>State</td>
        <td>Zip Code</td>
        <td>Notice of Violation</td>
        <td>Regulatory Sector</td>
        <td>Regulatory Subsector</td>
        <td>Type of Notice</td>
        <td>Issuing Agency</td>
        <td>Violation Type</td>
      </tr>
    </thead>
  )
}

const Reset = () => {
  return(
    <Form style={{textAlign:"center"}}>
      <h4>View data below.</h4>
      <h4>Reset to try again.</h4>
      <Button type="submit" size="sm">Reset</Button>
    </Form>
  )
}

export default App;
