import React from 'react';
import { isNullOrUndefined } from 'util';
import { Enforcement } from "./Enforcement";
import { ViolList } from "./ViolList";

export const FacilityDisplay = (props) => {
  console.log(props.facilityInfo)
  if (!isNullOrUndefined(props.facilityInfo)) {
    return (props.facilityInfo.map((facData, index) => {
      if (facData!==undefined){
        return (
          <IndFacDisp 
          key={index} 
          facData={facData[0]} 
          failedFacilities={props.failedFacilities}/>);
      } else {return null}
    }));
  }
  else {
    return null;
  }
};

const IndFacDisp = (props) => {
  const facilityType = () => {
    if (props.facData.PWS_TYPE_CODE === 'TNCWS') {
      return 'Transient Non-Community Water System';
    }
    else if (props.facData.PWS_TYPE_CODE === 'NTNCWS') {
      return 'Non-Transient Non-Community Water System';
    }
    else if (props.facData.PWS_TYPE_CODE === 'CWS') {
      return 'Community Water System';
    }
    else {
      return 'Unknown';
    }
  };
  
  if (props.facData.failed===true){
    return <FailedFacility facData={props.facData} />
  }else if (props.facData.VIOLATION===undefined || props.facData.VIOLATION===null){
    return <NoViol facData={props.facData} facilityType={facilityType}/>
  } else if (props.facData.VIOLATION.length > 0) {
    return (props.facData.VIOLATION.map((violation, index) => {
      return (<tbody key={index}>
        <ViolList facData={props.facData} violation={violation} facilityType={facilityType} />
        <Enforcement enfId={violation.LATEST_ENFORCEMENT_ID} pwsId={props.facData.PWSID} />
      </tbody>);
    }));
  } else {
    const violation = props.facData.VIOLATION.VIOLATION_ROW;
    return (<tbody key={0}>
      <ViolList facData={props.facData} violation={violation} facilityType={facilityType} />
      <Enforcement enfId={violation.LATEST_ENFORCEMENT_ID} pwsId={props.facData.PWSID} />
    </tbody>);
  }
};

const FailedFacility = (props) => {
  return (
    <tbody>
      <tr>
        <td>{props.facData.PWSID}</td>
        <td colSpan="11">Data retrieval failure.</td>
      </tr>
      <tr>
        <td style={{backgroundColor:"Blue"}}></td>
        <td colSpan="10">Nothing to display</td>
        <td style={{backgroundColor:"Blue"}}></td>
      </tr>
    </tbody>
  )
}

const NoViol = (props) => {
  return (
    <tbody>
      <tr>
        <td>{props.facData.PWSID}</td>
        <td>{props.facData.PWS_NAME}</td>
        <td>{props.facData.ADDRESS_LINE1}</td>
        <td>{props.facData.CITY_NAME}</td>
        <td>{props.facData.STATE_CODE}</td>
        <td>{props.facData.ZIP_CODE}</td>
        <td>N</td>
        <td>NA</td>
        <td>{props.facilityType()}</td>
        <td>NA</td>
        <td>NA</td>
        <td>NA</td>
      </tr>
      <tr>
        <td style={{backgroundColor:"Blue"}}></td>
        <td colSpan="10">Nothing to display</td>
        <td style={{backgroundColor:"Blue"}}></td>
      </tr>
    </tbody>
  )
}